.myjumbo {
  background-position: bottom;
  background-repeat: no-repeat;
  background-color: white;
  background-size: cover;
  color: #efefef;
  height: 700px;
  position: relative;
  justify-content: center;

  align-items: center;
  padding-top: 0px;
}
.video {
  position: absolute;
  z-index: 0;
  padding-top: none;
  overflow: hidden;
}
@media (min-aspect-ratio: 16/9) {
  .video {
    width: 100%;
    height: auto;
  }
}
@media (max-aspect-ratio: 16/9) {
  .video {
    width: auto;
    height: 100%;
  }
}
@media (max-width: 767 px) {
  .video {
    max-width: 100%;
    height: auto;
  }
}
.hi {
  background-image: url(./background.jpg);
  background-size: cover;
}
.btnmain {
  text-decoration: none;
  border: 3px solid white;
  background: none;
  padding: 4px;
  color: blue;
  font-family: "Raleway";
  font-size: 21px;
  cursor: pointer;
  transition: 0.8s;
  position: relative;
  justify-content: space-around;
  align-items: center;
  z-index: 10;
}
.btnshop {
  text-decoration: none;
  border: 3px solid white;
  background: none;
  z-index: 11;
  padding: 4px;

  color: white;
  font-family: "Raleway";
  font-size: 21px;
  cursor: pointer;
  transition: 0.8s;

  position: relative;
}
.btnshop:hover {
  color: grey;

  border: 3px solid white;
  z-index: 100;
}
.btnshop::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 0%;
  background: white;
  z-index: 10;
  transition: 0.8s;
  top: 0;
  border-radius: 0 0 50% 50%;
  color: grey;
}
.btnshop:hover::after {
  height: 180%;
  color: grey;
  z-index: -10;
}
.jumboShop {
  background-image: url(./shopsnow.jpg);
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  color: black;
  height: 480px;
  font-family: "Raleway";
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.shopwords {
  color: black;

  font-family: "Raleway";

  display: flex;
  justify-content: center;
  align-items: center;
}

.jumboAbout {
  background-image: url(./bridge.jpg);
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  color: #efefef;
  height: 480px;
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-family: "Raleway";
}

.jumboWords {
  color: black;

  font-family: "Raleway";

  display: flex;
  justify-content: center;
  align-items: center;
}
.words {
  display: flex;
  justify-content: space-around;
  align-items: center;

  vertical-align: middle;
  padding-top: 0px;
}
.footerinsta {
  color: white;
  margin: 0 1rem;
  display: inline-block;
  transition: transform 250ms;
  font-size: 24px;
  text-align: center;
  align-items: center;
}
.footerinsta:hover {
  color: grey;
}
.bodyCard {
  padding: 30px;
  margin: 0px;
  width: 600px;

  border: 3px solid #eee;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.3);
  margin-top: 20px;
  margin-bottom: 20px;
  background-color: white;
  justify-content: space-around;
  flex-wrap: wrap;
}
.bodyCard img {
  width: 100%;
  padding: 0px;
}
.bodyCard p {
  margin: 20px;
  padding: 0px;
  font: "Raleway";
  font-size: 17px;
  letter-spacing: 1px;
}
.bodyCard h2 {
  margin: 20px;
  padding: 0px;
  font: "Raleway";
  font-size: 35px;
  letter-spacing: 1px;
}
.bodyCard h3 {
  margin: 20px;
  padding: 0px;
  font: "Raleway";
  font-size: 25px;
  letter-spacing: 1px;
}
.buy {
  height: 40px;
  width: 90%;
  margin: 0px 5% 20px;
  border: none;
  box-shadow: 0px 5px 9px rgba(0, 0, 0, 0.3);
}
.cards {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: auto auto auto;
  padding: 30px;
  box-sizing: border-box;
  grid-gap: 30px;
}
.navbar {
  background-color: #fafafa;
}
.productDescription {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.Announcement {
  background-color: #db5c50;
  height: 27px;
  vertical-align: bottom;

  padding-bottom: 0px;
  padding-top: 5px;
}
.drop {
  font-size: 14px;

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.maintext {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-self: center;

  flex-direction: column;
  padding-top: 30vh;
}

.btnbox {
  padding-top: 10vh;

  position: relative;
  overflow: hidden;
  display: block;
}
